nav button{
    background: none;
    border:0;
    display: inline-block;
}

nav a{
    text-decoration: none;
}
.nav-inner{
    padding: 3px 15px 3px 15px;
    margin-right: 10px;

    border-radius: 7px;
    background: #ffffff;
    box-shadow:  5px 5px 10px #808080, -5px -5px 10px #ffffff;
    font-size: large;
}
.nav-current{
    color: blue;
    font-weight: bold;

}

.nav-non-current{
    color: silver;
}

.post-date{
    font-size:5;
    color: grey;
}
.post-author{
    font-size:5;
    color:grey;
}